<template>
  <div class="body">
    <el-card class="box-card">
      <el-row :gutter="20" style="margin-top:5px;">
        <el-col :span="6">
          <div class="bar">
            <div class="title"></div>
            <el-select v-model="searchOptions.searchParams.customerName" clearable>
              <el-option
                v-for="item in searchOptions.options.customerNameOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="bar">
            <div class="title"></div>
            <el-select v-model="searchOptions.searchParams.brandName" clearable>
              <el-option
                v-for="item in searchOptions.options.brandNameOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="bar">
            <div class="title"></div>
            <el-select v-model="searchOptions.searchParams.clothingLevel" clearable>
              <el-option
                v-for="item in searchOptions.options.clothingLevelOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="6">
          <div class="bar">
            <div class="title"></div>
            <el-select v-model="searchOptions.searchParams.rangeName" clearable>
              <el-option
                v-for="item in searchOptions.options.rangeNameOptions"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="20" style="margin-top: 30px; margin-bottom: 5px;">
        <el-col :span="6">
          <div class="bar">
            <div class="title"></div>
            <el-select v-model="searchOptions.searchParams.name" clearable>
              <el-option
                v-for="item in searchOptions.options.planNameOptions"
                :key="item.name"
                :label="item.name"
                :value="item.name"
              ></el-option>
            </el-select>
          </div>
        </el-col>
        <el-col :span="9">
          <div class="bar">
            <div class="title"></div>
            <el-date-picker
              style="margin-left: 20px"
              v-model="searchOptions.searchParams.dateRange"
              type="daterange"
              range-separator=""
              start-placeholde=""
              end-placeholde=""
              clearable
            ></el-date-picker>
          </div>
        </el-col>
        <el-col :span="6">
          <el-switch
            v-model="isSelfMadePlan"
            @change="planTypeSwitchChange"
            inactive-color="#13ce66"
            active-text=""
            inactive-text="">
          </el-switch>
        </el-col>
        <el-col :span="2">
          <el-button type="primary" icon="el-icon-search" @click="handleSearch"></el-button>
        </el-col>
      </el-row>
    </el-card>
    <el-card class="box-card">
      <el-row :gutter="20">
        <!-- <el-col :span="3" v-if="isSelfMadePlan">
          <el-button type="primary" size="small" @click="deletePlan" ></el-button>
        </el-col> -->
        <!-- <el-col :span="3" v-if="isSelfMadePlan">
          <el-button type="primary" size="small" @click="submitPlan" ></el-button>
        </el-col> -->
        <el-col :span="3" v-if="!isSelfMadePlan">
          <el-button type="primary" size="small" @click="addPlanChild"></el-button>
        </el-col>
        <el-col :span="4" v-if="!isSelfMadePlan">
          <el-button type="primary" size="small" @click="changeSubPlanOrder"></el-button>
        </el-col>
        <el-col :span="3" v-if="!isSelfMadePlan">
          <el-button type="primary" size="small" @click="addException"></el-button>
        </el-col>
      </el-row>
      <el-table
        :data="tableDataShow"
        max-height="400"
        @selection-change="changeCheckBoxFun"
        :stripe="true"
        :highlight-current-row="true"
        style="width: 100%; margin-top: 20px"
      >
        <el-table-column width="50" type="selection" align="center"></el-table-column>
        <el-table-column width="50" type="index" label="" align="center"></el-table-column>
        <el-table-column prop="id" v-if="false"></el-table-column>
        <el-table-column prop="number" label="" align="center"></el-table-column>
        <el-table-column prop="name" label="" align="center"></el-table-column>
        <el-table-column prop="rangeNumber" label="" align="center"></el-table-column>
        <el-table-column prop="customerName" label="" align="center"></el-table-column>
        <el-table-column prop="brandName" label="" align="center"></el-table-column>
        <el-table-column prop="rangeName" label="" align="center"></el-table-column>
        <el-table-column prop="createrName" label="" align="center"></el-table-column>
        <el-table-column prop="deptName" label="" align="center"></el-table-column>
        <el-table-column prop="createTime" label="" align="center"></el-table-column>
        <el-table-column prop="parentName" label="" align="center"></el-table-column>
        <el-table-column prop="state" label="" align="center"></el-table-column>
        <!-- <el-table-column prop="havePlan" label="" align="center">
          <template slot-scope="scope">
            <p v-if="scope.row.havePlan"></p>
            <p v-else></p>
          </template>
        </el-table-column> -->
        <el-table-column label="" width="150" align="center">
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="ToSearchException(scope.row)"
              type="text"
              size="small"
              v-if="scope.row.haveException"
            ></el-button>
            <p v-else></p>
          </template>
        </el-table-column>
        <el-table-column fixed="right" label="" width="150" align="center">
          <template slot-scope="scope">
            <el-button @click.native.prevent="getPlanDetail(scope.row)" type="text" size="small"></el-button>
            <el-button
              v-if="isSelfMadePlan && (scope.row.state === '' || scope.row.state === '')"
              @click.native.prevent="ModifyPlanDetail(scope.row)"
              type="text"
              size="small"
            ></el-button>
            <el-button
              v-if="isSelfMadePlan && (scope.row.state === '' || scope.row.state === '')"
              @click.native.prevent="submitPlan(scope.row)"
              type="text"
              size="small"
            ></el-button>
            <el-button
              v-if="isSelfMadePlan && (scope.row.state === '' || scope.row.state === '')"
              @click.native.prevent="deleteOnePlan(scope.row.id)"
              type="text"
              size="small"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    <!--  -->
        <div class="block">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="pagination.currentPage"
            :page-sizes="pagination.pageSizes"
            :page-size="pagination.pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="pagination.total"
          ></el-pagination>
        </div>
    </el-card>

    <el-dialog title=""  :visible.sync="subPlanOrderModificationDialogVisible" :modal="false">
      <el-table
        :data="subPlanTableData"
        highlight-current-row
        style="width: 100%"  >
          <!-- <el-table-column  type="selection"  width="55px"></el-table-column> -->
          <el-table-column  type="index"  label="" width="70px"></el-table-column>
          <el-table-column  prop="order"  label="" width="70"></el-table-column>
          <el-table-column  prop="name"  label=""  width="100px"></el-table-column>
          <el-table-column  prop="number"  label=""  width="100px"></el-table-column>
          <el-table-column  prop="startDate"  label=""  width="100px"></el-table-column>
          <el-table-column  prop="endDate"  label=""  width="100px"></el-table-column>
          <el-table-column  prop="createrName"  label=""  width="100px"></el-table-column>
          <el-table-column  prop="deptName"  label=""  width="100px"></el-table-column>
          <el-table-column  prop="createTime"  label=""  width="100px"></el-table-column>
          <el-table-column label="" width="150px" fixed="right">
            <template slot-scope="scope">
              <el-button  size="mini"  :disabled="scope.$index===0"  @click="moveUp(scope.$index,scope.row)">
                <i class="el-icon-arrow-up"></i>
              </el-button>
              <el-button  size="mini"  :disabled="scope.$index===(subPlanTableData.length-1)"  @click="moveDown(scope.$index,scope.row)">
                <i class="el-icon-arrow-down"></i>
              </el-button>
              <!-- <el-button type="info" size="mini" round v-if="scope.$index===0"></el-button> -->
            </template>
          </el-table-column>
        </el-table>
      <el-button  type="primary" @click="subPlanOrderConfirm"></el-button>
    </el-dialog>
  </div>
</template>

<script>
import { error } from 'util';
import consoleSidebarVue from '../../components/layout/consoleSidebar.vue';
export default {
  data() {
    return {
      subPlanOrderModificationDialogVisible:false,
      tableDataShow:[],
      isSelfMadePlan:false,
      searchOptions: {
        searchParams: {
          customerName: "",
          brandName: "",
          clothingLevel: "",
          rangeName: "",
          name:"",
          dateRange: ""
        },
        options: {
          customerNameOptions: [],
          brandNameOptions: [],
          clothingLevelOptions: [],
          rangeNameOptions:[],
          planNameOptions:[],
        }
      },
      tableData: [],
      pagination: {
        currentPage: 1,
        pageSizes: [10, 20, 30, 40, 50],
        pageSize: 10,
        total: 0
      },
      selectedData: [],

      subPlanTableData: [],
    };
  },
  created: function () {
    console.log('');

    //
    this.$axios
      .get(`${window.$config.HOST}/baseInfoManagement/getCustomerName`)
      .then(response => {
        this.searchOptions.options.customerNameOptions = response.data;
      })
      .catch(error => {
        console.log("!");
      });

    //
    this.$axios
      .get(`${window.$config.HOST}/baseInfoManagement/getClothingLevelName`)
      .then(response => {
        this.searchOptions.options.clothingLevelOptions = response.data;
      })
      .catch(error => {
        console.log("");
      });

    //
    this.$axios
      .get(`${window.$config.HOST}/baseInfoManagement/getBrandName`,{
        params:{customerId:""}
      })
      .then(response => {
        this.searchOptions.options.brandNameOptions = response.data;
      })
      .catch(error => {
        console.log("");
      });

    //
    this.$axios
      .get(`${window.$config.HOST}/infoManagement/getRangeName`,{
        params:{brandId:""}
      })
      .then(response => {
        this.searchOptions.options.rangeNameOptions = response.data;
      })
      .catch(error => {
        console.log("");
      });

    //
    var param = {
      stage: "manage"
    }
    this.$axios
      .get(`${window.$config.HOST}/planManagement/getDistributedPlanList`)
      .then(response => {
        this.tableData = response.data;
        this.tableData.forEach(element=>{
        if(element.isRoot){
            element.parentName = "";
          }
        });

        //
        this.pagination.total=response.data.length;
        let i = (this.pagination.currentPage-1) * this.pagination.pageSize;
        let k = (this.pagination.currentPage-1) * this.pagination.pageSize;
        this.tableDataShow=[];
        
        for(;i-k<this.pagination.pageSize&&i<this.tableData.length;i++)
        {
          this.tableDataShow.push(this.tableData[i]);
        }
        this.searchOptions.options.planNameOptions = response.data;
      })
      .catch(error => {
        console.log("");
      });
  },
  methods: {
    ToSearchException(row) {
      console.log(""+row.id);
      this.$router.push({
        name: "exceptionManagement",
        params: {
          planId: row.id,
          customerId: row.customerId,
          customerName: row.customerName,
          brandId: row.brandId,
          brandName: row.brandName,
          rangeId: row.rangeId,
          rangeName: row.rangeName,
        }
      });
    },    
    handleSizeChange(val) {
      this.pagination.pageSize=val;
      console.log("+"+this.pagination.pageSize)
      this.handleSearch();
    },
    handleCurrentChange(val) {
      this.pagination.currentPage=val;
        this.handleSearch();
    },
    addPlanChild() {
      if (this.selectedData.length === 1) {
        let data = this.selectedData[0];
        var param={
          flag: 1,
          goback: "planManagement",
          client: data.customerName,
          brand: data.brandName,
          series: data.rangeName,
          id:data.id,
          plantype: data.type,
          planobj: data.planObject,
          TopPlan: data.parentId,
          TopPlanName: data.parentName?data.parentName:"",
          planName:data.name,
          projectType:data.projectType,
          number:data.number,
          dataStart:data.startDate,
          dataEnd:data.endDate,
          productDate:data.productDate,
          productDateType:data.productDateType,
          productId:data.productId,
          proposal:data.proposal,
          note:data.note,
          description:data.description,
        };
        console.log(param);
        this.$router.push({
          name: "planMakeIndex",
          params: param
        });
      } else if (this.selectedData.length === 0) {
        this.$message.error("");
      } else {
        this.$message.error("");
      }
    },
    deletePlan() {
      if (this.selectedData.length === 0) {
        this.$message.error("");
      } else {
        this.selectedData.forEach(element=>{
            console.log(element.id);
            this.deleteOnePlan(element.id);
            // this.$axios
            //   .delete(`${window.$config.HOST}/planManagement/deletePlan`,{
            //     params:{id:element.id}
            //   })
            //   .then(response=>{
            //     if(response.data < 0 ){
            //       this.$message.error(element.id + "!");
            //     }else{
            //       this.$message({
            //         type:"success",
            //         message: element.id+"!"
            //       });
            //       this.handleSearch()
            //     }
            //   })
            //   .catch(error=>{
            //     this.$message.error(element.id + "!");
            //   })
          });
      }
    },
    //
    submitPlan(row){
      // 
      // if (this.selectedData.length === 0) {
      //   this.$message.error("");
      // } else {
      //   this.selectedData.forEach(element=>{
      //       console.log(element.id);
      //       this.$axios
      //         .get(`${window.$config.HOST}/planManagement/submitPlan`,{
      //           params:{id:element.id}
      //         })
      //         .then(response=>{
      //           if(response.data < 0 ){
      //             console.log(element.name + "()!");
      //             this.$message.error(element.id + "!");
      //           }else{
      //             this.$message({
      //               type:"success",
      //               message: element.id+"!"
      //             });
      //             this.handleSearch()
      //           }
      //         })
      //         .catch(error=>{
      //           console.log(element.name + "!");
      //           this.$message.error(element.id + "!");
      //         })
      //     });
      // }

      //
      this.$axios
        .get(`${window.$config.HOST}/planManagement/submitPlan`,{
          params:{id:row.id}
        })
        .then(response=>{
          if(response.data < 0 ){
            console.log(row.name + "()!");
            this.$message.error(row.id + "!");
          }else{
            this.$message({
              type:"success",
              message: row.id+"!"
            });
            this.handleSearch();
          }
        })
        .catch(error=>{
          console.log(row.name + "!");
          this.$message.error(row.id + "!");
        })
    },
    //
    changeSubPlanOrder() {
      // this.$message("");
      if (this.selectedData.length === 1) {
        var param = {
          id: this.selectedData[0].id,
        };
        console.log(param);
        this.$axios
          .get(`${window.$config.HOST}/planManagement/getChildrenPlanList`,{
            params:param
          })
          .then(response=>{
            // console.log(response.data);
            this.subPlanTableData=response.data;
            this.subPlanTableData.sort(function(a,b){
              return a.order-b.order;
            });
          })
          .catch(error=>{
            console.lopg("");
          });
        this.subPlanOrderModificationDialogVisible = true;
      } else if (this.selectedData.length === 0) {
        this.$message.error("");
      } else {
        this.$message.error("");
      }
    },
    subPlanOrderConfirm(){
      var param = [];
      var len = this.subPlanTableData.length;
      for(var i = 0; i < len; i++){
        param.push({
          id:this.subPlanTableData[i].id,
          order: i,
        });
      }
      console.log(param);
      this.$axios
        .post(`${window.$config.HOST}/planManagement/adjustPlanOrder`,param)
        .then(response=>{
          if(response.data < 0){
            console.log("");
            this.$message.error("");
          }else{
            console.log("");
            this.$message({
              type:"success",
              message:"!"
            });
          }
        })
        .catch(error=>{
          console.log("");
          this.$message.error("");
        })
      this.subPlanOrderModificationDialogVisible = false;
    },
    addException() {
      if (this.selectedData.length === 0) {
        this.$message.error("");
      } else {
        this
          .$prompt("", "", {
            confirmButtonText: "",
            cancelButttonText: "",
            inputPattern: /\S/,
            inputErrorMessage: ""
          })
          .then(({ value }) => {
            console.log(value);
            this.selectedData.forEach(element=>{
              var params = {
                planId : element.id,
                cause : (value==="")?"":value,
              };
              // console.log(params);
              this.$axios.post(`${window.$config.HOST}/planManagement/addException`,params)
                .then(response=>{
                  if(response.data < 0){
                    this.$message.error(element.name + "");
                  }else{
                    this.$message({
                      type:"success",
                      message:element.name+""
                    })
                  }
                })
                .catch(error=>{
                  this.$message.error(element.name + "");
                })
            });
          });
      }
    },
    changeCheckBoxFun(val) {
      this.selectedData = val;
    },
    getPlanDetail(row) {
      var param={
        flag: 0,
        goback: "planManagement",
        client: row.customerName,
        brand: row.brandName,
        series: row.rangeName,
        id:row.id,
        plantype: row.type,
        planobj: row.planObject,
        TopPlan: row.parentId,
        TopPlanName: row.parentName?row.parentName:"",
        planName:row.name,
        projectType:row.projectType,
        number:row.number,
        dataStart:row.startDate,
        dataEnd:row.endDate,
        productDate:row.productDate,
        productDateType:row.productDateType,
        productId:row.productId,
        proposal:row.proposal,
        note:row.note,
        description:row.description,
      };
      console.log(param);
      this.$router.push({
        name: "planMakeIndex",
        params: param
      });
    },
    ModifyPlanDetail(row) {
      var param={
        flag: 1,
        goback: "planManagement",
        client: row.customerName,
        brand: row.brandName,
        series: row.rangeName,
        id:row.id,
        plantype: row.type,
        planobj: row.planObject,
        TopPlan: row.parentId,
        TopPlanName: row.parentName?row.parentName:"",
        planName:row.name,
        projectType:row.projectType,
        number:row.number,
        dataStart:row.startDate,
        dataEnd:row.endDate,
        productDate:row.productDate,
        productDateType:row.productDateType,
        productId:row.productId,
        proposal:row.proposal,
        note:row.note,
        description:row.description,
      };
      console.log(param);
      this.$router.push({
        name: "planMakeIndex",
        params: param
      });
    },
    deleteOnePlan(planid) {
      console.log(" "+planid);
      this.$axios
        .delete(`${window.$config.HOST}/planManagement/deletePlan`,{
          params:{id:planid}
        })
        .then(response=>{
          if(response.data < 0){
            this.$message.error(planid+"");
            console.log("");
          }else{
            this.handleSearch();
            console.log("");
            this.$message({
              type:"success",
              message:planid+""
            });
          }
        })
        .catch(error=>{
          this.$message.error(planid+"");
        });
    },
    // 
    changeDate(date) {
      if(!date){
        return undefined;
      }else{
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? "0" + m : m;
        var d = date.getDate();
        d = d < 10 ? "0" + d : d;
        // var h = date.getHours();
        // var minute = date.getMinutes();
        // minute = minute < 10 ? "0" + minute : minute;
        // var second = date.getSeconds();
        // second = minute < 10 ? "0" + second : second;
        // return y + "-" + m + "-" + d + " " + h + ":" + minute + ":" + second;
        return y + "-" + m + "-" + d;
      }
    },
    //
    handleSearch(){
      var param = {
        customerId: (this.searchOptions.searchParams.customerName==="")?undefined:this.searchOptions.searchParams.customerName, 
        brandId: (this.searchOptions.searchParams.brandName==="")?undefined:this.searchOptions.searchParams.brandName, 
        rangeId: (this.searchOptions.searchParams.rangeName==="")?undefined:this.searchOptions.searchParams.rangeName,  
        name: (this.searchOptions.searchParams.name==="")?undefined:this.searchOptions.searchParams.name, 
        clothingLevelId :(this.searchOptions.searchParams.name==="")?undefined:this.searchOptions.searchParams.name, 
        startDate: this.changeDate(this.searchOptions.searchParams.dateRange[0]),
        endDate:this.changeDate(this.searchOptions.searchParams.dateRange[1]),
      };

      if(this.isSelfMadePlan){
        param.stage = "manage";
        console.log(param);
        this.$axios
          .get(`${window.$config.HOST}/planManagement/getPlanList`,{
            params:param
          })
          .then(response=>{
            this.tableData = response.data;
            this.tableData.forEach(element=>{
              if(element.isRoot){
                element.parentName = "";
              }
            });

            console.log(this.tableDataShow);
            //
            this.pagination.total=response.data.length;
            let i = (this.pagination.currentPage-1) * this.pagination.pageSize;
            let k = (this.pagination.currentPage-1) * this.pagination.pageSize;
            this.tableDataShow=[];
            
            for(;i-k<this.pagination.pageSize&&i<this.tableData.length;i++)
            {
              this.tableDataShow.push(this.tableData[i]);
            }
          })
          .catch(error=>{
            this.$message.error("!");
          });
      }else{
        console.log(param);
        this.$axios
          .get(`${window.$config.HOST}/planManagement/getDistributedPlanList`,{
            params:param
          })
          .then(response=>{
            this.tableData = response.data;
            this.tableData.forEach(element=>{
              if(element.isRoot){
                element.parentName = "";
              }
            });

            //
            this.pagination.total=response.data.length;
            let i = (this.pagination.currentPage-1) * this.pagination.pageSize;
            let k = (this.pagination.currentPage-1) * this.pagination.pageSize;
            this.tableDataShow=[];
        
            for(;i-k<this.pagination.pageSize&&i<this.tableData.length;i++)
            {
              this.tableDataShow.push(this.tableData[i]);
            }
          })
          .catch(error=>{
            this.$message.error("!");
          });
      }
    },
    planTypeSwitchChange(){
      this.pagination.currentPage=1;
      this.tableData = [];
      this.handleSearch();
